@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap');

:root {
  --blue: rgb(0, 152, 253);
  --white: #ffffff;
  --black:#fff;
  --danger:#d9534f;
  --gray:rgb(223, 224, 229);
  --light-gray:rgba(223, 224, 229,0.7);
  --pad15: 15px 10px;
  --pad-btn: 10px 15px
  
   
}

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  padding: 0;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

img {
  border: none;
  max-width: 100%;
}

a, img {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

textarea:focus, input:focus, select:focus {
  outline: none;
}
/*-------------------------------------headings-------------------------------  */
h5{
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 1em;
}
h4{
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 1em;
}
/*-------------------------------------sizing & spacing-------------------------------  */
.mb15
{
  margin-bottom: 15px;
}
.w100
{
  width: 100px;
}
/*-------------------------------------buttons-------------------------------  */
button
{
  border-radius: 5px;
  border: none;
  cursor: pointer;

}
button:hover{
  box-shadow: rgb(0 0 0 / 27%)1px 0px 3px 1px;
}
button.submit
{
 background-color: var(--blue);
 color: var(--white);
 border-radius: 5px;

    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
/*-------------------------------------Display-------------------------------  */
.flex-sb
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.flex-e

  {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;
}

/*------------------------------------- left-panel-------------------------------  */
#left-menu
{
  width:180px;
  position: fixed;
  top:0;
  left:0;
  height: 100%;
  border-right:1px solid var(--gray);
  background-color: var(--white);

  z-index: 2;
    
}
.left-link
{
    padding: var(--pad-btn);
    font-weight: 500;
    border-radius: 10px;

  
}
.left-link:hover
{
  background-color: var(--light-gray);
}
.left-link svg{
  display: block;
  width: 24px;
  height: auto;
}

#v-menu
{
  position: relative;
  background: var(--white);
  z-index:2;
  height: 100%;
  padding: var(--pad15);
}

#left-panel
{
  width:360px;
  position: fixed;
  top:0;
  left:180px;
  height: 100%;
  border-right:1px solid var(--gray);
  background-color: var(--white);
  padding: var(--pad15);
  transform: translateX(-100%); 
  transition: transform  0.4s;
  z-index: 1;
}
#left-panel.active
{
  transform: translate(0);
}
.back
{
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 30px;
  padding: 5px;

}
.back svg{
  width:100%;
  height:  auto;

}
.numberBox
{
  padding: 25px 0 0 0 ;
 
}
.numberBox button
{
  width:140px;
  height: 35px;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--gray);
  line-height: 1;
  font-size: 12px;
}

.numberBox p{
  display: flex;
  width:70px
}
/*-------------------------------------right-panel-------------------------------  */
#right-panel
{
  width:350px;
  position: fixed;
  top:0;
  right:0;
  height: 100%;
  border-left:1px solid var(--gray);
  background-color: var(--white);
  padding: var(--pad15);
  z-index: 5;

}
#content
{
  width: calc(100% - 530px);
  background-color: var(--white);
  margin: auto;

}
/*------------------------------------- Groups-------------------------------  */
.p-group
{
  padding:15px 0 ;
  border-bottom:1px solid var(--gray);
}
.plc-h
{
 padding: 30px 20px;
  border: 1px solid #ddd;
}

.p-row 
{
  display: flex;
  justify-content: flex-start;

}
.p-row  label
{
  margin-right: 10px;
  width:120px;
}


.selected
{

  border: 1px solid var(--blue);


}
table,tr,td
{
  position: relative;
  z-index: 1;
}
.options-holder
{
  display: flex;
  align-items: center;
  column-gap: 5px;
  margin-bottom: 15px;
  
}

.options-holder button
{
  height: 50px;
  width: 25%;
}
.options-holder button.active
{
  background-color: var(--blue);
  color: #fff;
}
input[type=text],textarea,select
{
  resize: none;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid var(--gray);
  width: 100%;
  font-family: inherit;
}
.text-editor
{
   width: 100%;
  
   border: 1px solid var(--gray);
   border-radius: 5px;

} 

.text-control
{
  padding: 5px;
  border-bottom: 1px solid var(--gray);
}
.btn-editor
{
  background-color: transparent;
  width: 20px;
  height: 20px;
  margin-right: 5px;
}
.btn-editor svg{
  display: block;
  width: 100%;
  height :auto;
}
.main-editor
{
  padding: 5px;
  min-height: 300px;
  border: none;
  text-align: center;
}
.main-editor:focus
{
  outline: none;
}
td{
  font-weight: 400;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  mso-table-lspace: 0;
  mso-table-rspace: 0;
  table-layout: fixed;
}

  table td {
      border-collapse: collapse;
      mso-table-lspace: 0;
      mso-table-rspace: 0;
  }

img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  max-width: 100%;
}

a, a:visited {
  text-decoration: none;
}

select option
{
  padding: 5px;
}